@import 'src/static/styles/mixins&vars';
.container{
    margin: 0 auto;
    max-width: 780px;
    padding: 0 20px;
    position: relative;
}
.container_2{
    max-width: 1240px;
    margin: 0 auto;
    padding: 20px;
    position: relative;
}
.project_open{
    padding-top: 60px;
    font-size: 14px;
}
.project_title{
    text-align: center;
    h1:last-child{
        margin-left: 400px;
    }
}
.max_600{
    max-width: 650px;
}
.max_500{
    max-width: 500px;
}
.lines{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height:180px;
    width: 140px;
    transform: rotate(-45deg);
    span{
        width: 100%;
        height: 1px;
        background-color:#ACACAC;
        opacity: 0.5;
    }
}
.image_left_capt{
    display: flex;
    align-items: center;
    margin-top: 85px;
    justify-content: space-between;
    padding-right: 10%;
    h4{
        max-width: 350px;
        margin-right: 30px;
    }
    &_img{
        width: 445px;
        height: 424px;
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: center center;
        }
    }
}
.circle_info{
    display: flex;
    justify-content: center;
    flex-direction: column;
    width: 480px;
    height: 480px;
    background-color: #FFF5F5;
    border-radius: 50%;
    padding: 80px;
    margin-top: -70px;
    h3{
        margin-bottom: 25px;
        text-align: left;
    }
}
.project_open{
    h5{
        text-transform: uppercase;
    }
    h4{
        text-transform: uppercase;
    }
}
.block_2{
    .max_500{
        margin-top: 30px;
    }
}
.text_flow{
    margin-top: 80px;
}
.circle_bg{
    @include bg_circle;
}
.block_3{
    .text_flow{
        position: relative;
        z-index: 2;
    }
    .circle_bg{
        right: -150px;
        top: 200px;
        z-index: 0;
        &:first-child{
            right: auto;
            left: -100px;
            top: 750px;
        }
    }
}
.block_imgs{
    background-color:#333333;
    padding-top: 50px;
    margin-top: 300px;
    &>.just_btwn{
        margin-bottom: 110px;
    }
    .two_sm_img{
        margin-top: -130px;
        padding-left: 10vw;
        div{
            width: 500px;
            height: 320px;
            &:first-child{
                margin-bottom: 200px
            }
            img{
                @include img_cover;
            }
        }
    }
    .sm_img_cations{
        div{
            width: 545px;
            height: 320px;
            img{
                @include img_cover;
            }
        }
        h3{
            color: #fff;
            margin-top: 200px;
        }
    }
    &_big{
        width: 100%;
        max-height: 790px;
        position: relative;
        overflow: hidden;
        img{
            width: 100%;
            max-height: 100%;
            object-fit: cover;
            object-position: center center;
        }
    }

}
.block_4{
    position: relative;
    margin-top: 300px;
    .max_600{
        position: relative;
        z-index: 2;
    }
    .circle_bg{
        margin-left: -100px;
        z-index: 1;
        top: -200px;
    }
    .lines{
        position: absolute;
        z-index: 0;
        left: -150px;
        top: calc(-100% - 20px);
    }
}
.block_imgs_2{
    margin-top: 25px;
    margin-bottom: 130px;
    div{
        width: 700px;
        height: 410px;
        img{
            @include img_cover;
        }
    }
    div:first-child{
        margin-right: 0;
        margin-left: auto;
        margin-bottom: 80px;
    }
    div:last-child{
        margin-left: 10vw;
    }
}
.text_flow_2{
    display: flex;
    flex-wrap: wrap;
    margin: 0 -15px;
    >.text_block {
        width: 50%;
        padding: 0 15px;
        margin-bottom: 50px;
    }
    &_add_info{
        background-color: #FFF5F5;
        width: 477px;
        height: 477px;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 20px;
        position: absolute;
        right: -150px;
        bottom: -120px;
        p{
            max-width: 260px;
        }
    }
}
.project_open{
    footer{
        background-color: #333333;
        color: #fff;
        padding: 120px 0;
        margin-top: 250px;
    }
    .footer_text{
        p{
            margin-top: 35px;
        }
    }
}
.get_in_touch_wrap{
    background-color: #FFF5F5;
    padding: 150px;
    border-radius: 50%;
    .get_touch{
        position: static !important;
    }
}
.proj_link{
    display: flex;
    align-items: center;
    color: #FFF5F5;
    
    &:last-child{
        color: #fff;
        margin-left: 200px;
        margin-top: 30px;
    }
    h3{
        margin: 0 20px;
    }
    &:hover{
        color: #FF6D6D;
        svg path{
            fill:#FF6D6D;
        }
    }
}
.fixed_footer{
    display: flex;
    align-items: center;
    min-height: 101px;
    width: 100vw;
    position: fixed;
    bottom: 0;
    background-color: #333333;
    z-index: 10;
    color: #fff;
    padding: 10px 10vw;
    justify-content: space-between;
    &_soc{
        display: flex;
        align-items: center;
        margin-right: 80px;
        >a{
            margin-right: 15px;
        }
    }
    .invested{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        width: 500px;
        &_bar{
            width: 100%;
            position: relative;
            height: 10px;
            margin: 0 40px;
            margin-bottom: 15px;
            &:after{
                content:'';
                width: 100%;
                height: 3px;
                background-color: #FFF5F5;
                position: absolute;
                top: 50%;
                transform: translate(0,-50%);
            }
           
        }
    }
}
.invested_bar{ 
    span{
        display: block;
        background-color:#438F29;
        position: absolute;
        top: 50%;
        transform: translate(0,-50%);
        z-index: 2;
    }
    span:first-child{
            height: 3px; 
    }
    span:last-child{
        border-radius: 50%;
         width: 23px;
         height: 23px;
    }
}